#sfc-app {
    width: 600px;
    text-align: left;
    margin: 0 auto 0 auto;
}

#sfc-app > h1 {
    font-size: 40px;
}

#sfc-app > h2 {
    width: 600px;
    margin: 50px auto 0 auto;
    padding-left: 20px;
}

#sfc-current-auth-panel {
    width: 600px;
    height: 500px;
    margin: 10px auto 0 auto;
    padding-left: 20px;
    text-align: left;
}

#sfc-current-auth-panel label {
    display: inline-block;
    text-align: left;
    font-weight: 500;
    width: 100px;
    margin: 0 0 10px 20px;
}